// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ –±—ñ–±–ª—ñ–æ—Ç–µ–∫—É —Å–ø–æ–≤—ñ—â–µ–Ω—å iziToast —Ç–∞ —ó—ó —Å—Ç–∏–ª—ñ
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ —Ñ—É–Ω–∫—Ü—ñ—é –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å –∑ Pixabay
import { getImagesByQuery } from './js/pixabay-api.js';

// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è —Ä–µ–Ω–¥–µ—Ä—É –≥–∞–ª–µ—Ä–µ—ó —Ç–∞ –µ–ª–µ–º–µ–Ω—Ç—ñ–≤ —ñ–Ω—Ç–µ—Ä—Ñ–µ–π—Å—É
import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions.js';

// –û—Ç—Ä–∏–º—É—î–º–æ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –Ω–∞ –µ–ª–µ–º–µ–Ω—Ç–∏ DOM
const form = document.querySelector('.form');
const loadMoreBtn = document.querySelector('.load-more-btn');
const gallery = document.querySelector('.gallery');

// –ó–º—ñ–Ω–Ω—ñ –¥–ª—è –∑–±–µ—Ä—ñ–≥–∞–Ω–Ω—è –ø–æ—Ç–æ—á–Ω–æ–≥–æ –∑–∞–ø–∏—Ç—É, —Å—Ç–æ—Ä—ñ–Ω–∫–∏ —Ç–∞ –∑–∞–≥–∞–ª—å–Ω–æ—ó –∫—ñ–ª—å–∫–æ—Å—Ç—ñ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤
let currentQuery = '';
let currentPage = 1;
let totalHits = 0;

// –ö—ñ–ª—å–∫—ñ—Å—Ç—å –∑–æ–±—Ä–∞–∂–µ–Ω—å –Ω–∞ –æ–¥–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É
const PER_PAGE = 15;

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫–∏ –ø–æ–¥—ñ–π –Ω–∞ —Ñ–æ—Ä–º—É —Ç–∞ –∫–Ω–æ–ø–∫—É "Load More"
form.addEventListener('submit', onSearch);
loadMoreBtn.addEventListener('click', onLoadMore);

// –û–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –Ω–∞–¥—Å–∏–ª–∞–Ω–Ω—è —Ñ–æ—Ä–º–∏
async function onSearch(event) {
  event.preventDefault(); // –ó—É–ø–∏–Ω—è—î–º–æ –ø–µ—Ä–µ–∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏

  // –û—Ç—Ä–∏–º—É—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è –∑–∞–ø–∏—Ç—É —Ç–∞ –æ—á–∏—â–∞—î–º–æ –ø—Ä–æ–±—ñ–ª–∏
  const searchQuery = event.target.elements['search-text'].value.trim();

  // –Ø–∫—â–æ –ø–æ–ª–µ –ø–æ—Ä–æ–∂–Ω—î ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–ø–µ—Ä–µ–¥–∂–µ–Ω–Ω—è
  if (!searchQuery) {
    iziToast.warning({
      title: 'Warning',
      message: 'Please enter a search term',
      position: 'topRight',
    });
    return;
  }

  // –û–Ω–æ–≤–ª—é—î–º–æ –ø–æ—Ç–æ—á–Ω–∏–π –∑–∞–ø–∏—Ç —ñ —Å–∫–∏–¥–∞—î–º–æ —Å—Ç–æ—Ä—ñ–Ω–∫—É
  currentQuery = searchQuery;
  currentPage = 1;

  // –û—á–∏—â–∞—î–º–æ –≥–∞–ª–µ—Ä–µ—é —Ç–∞ –ø—Ä–∏—Ö–æ–≤—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load More"
  clearGallery();
  hideLoadMoreButton();

  // –ü–æ–∫–∞–∑—É—î–º–æ –ª–æ–∞–¥–µ—Ä
  showLoader();

  try {
    // –û—Ç—Ä–∏–º—É—î–º–æ –¥–∞–Ω—ñ –∑ API
    const data = await getImagesByQuery(currentQuery, currentPage);

    // –Ø–∫—â–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–º–∏–ª–∫—É
    if (data.hits.length === 0) {
      iziToast.error({
        title: 'Error',
        message:
          'Sorry, there are no images matching your search query. Please try again!',
        position: 'topRight',
      });
      return;
    }

    // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ –∑–∞–≥–∞–ª—å–Ω—É –∫—ñ–ª—å–∫—ñ—Å—Ç—å –∑–æ–±—Ä–∞–∂–µ–Ω—å
    totalHits = data.totalHits;

    // –†–µ–Ω–¥–µ—Ä–∏–º–æ –≥–∞–ª–µ—Ä–µ—é –∑ –æ—Ç—Ä–∏–º–∞–Ω–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
    createGallery(data.hits);

    // –Ø–∫—â–æ —î –±—ñ–ª—å—à–µ –∑–æ–±—Ä–∞–∂–µ–Ω—å ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load More"
    if (totalHits > PER_PAGE) {
      showLoadMoreButton();
    } else {
      hideLoadMoreButton();
    }
  } catch (error) {
    // –£ –≤–∏–ø–∞–¥–∫—É –ø–æ–º–∏–ª–∫–∏ ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    iziToast.error({
      title: 'Error',
      message: 'Something went wrong. Please try again later.',
      position: 'topRight',
    });
    console.error(error);
  } finally {
    // –•–æ–≤–∞—î–º–æ –ª–æ–∞–¥–µ—Ä —Ç–∞ —Å–∫–∏–¥–∞—î–º–æ —Ñ–æ—Ä–º—É
    hideLoader();
    form.reset();
  }
}

// –û–±—Ä–æ–±–Ω–∏–∫ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—è –Ω–∞ –∫–Ω–æ–ø–∫—É "Load More"
async function onLoadMore() {
  currentPage += 1; // –ü–µ—Ä–µ—Ö–æ–¥–∏–º–æ –¥–æ –Ω–∞—Å—Ç—É–ø–Ω–æ—ó —Å—Ç–æ—Ä—ñ–Ω–∫–∏
  showLoader(); // –ü–æ–∫–∞–∑—É—î–º–æ –ª–æ–∞–¥–µ—Ä
  hideLoadMoreButton(); // –ü—Ä–∏—Ö–æ–≤—É—î–º–æ –∫–Ω–æ–ø–∫—É, –ø–æ–∫–∏ –Ω–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–∞—Ç—å—Å—è –¥–∞–Ω—ñ

  try {
    // –û—Ç—Ä–∏–º—É—î–º–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –Ω–∞—Å—Ç—É–ø–Ω–æ—ó —Å—Ç–æ—Ä—ñ–Ω–∫–∏
    const data = await getImagesByQuery(currentQuery, currentPage);

    // –î–æ–¥–∞—î–º–æ –Ω–æ–≤—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–æ –≥–∞–ª–µ—Ä–µ—ó
    createGallery(data.hits);

    // –û–±—á–∏—Å–ª—é—î–º–æ –∑–∞–≥–∞–ª—å–Ω—É –∫—ñ–ª—å–∫—ñ—Å—Ç—å —Å—Ç–æ—Ä—ñ–Ω–æ–∫
    const totalPages = Math.ceil(totalHits / PER_PAGE);

    // –Ø–∫—â–æ –æ—Å—Ç–∞–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∞ ‚Äî —Ö–æ–≤–∞—î–º–æ –∫–Ω–æ–ø–∫—É —Ç–∞ –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    if (currentPage >= totalPages) {
      hideLoadMoreButton();
      iziToast.info({
        title: 'Info',
        message: "We're sorry, but you've reached the end of search results.",
        position: 'topRight',
      });
    } else {
      showLoadMoreButton(); // –Ü–Ω–∞–∫—à–µ –ø–æ–∫–∞–∑—É—î–º–æ –∫–Ω–æ–ø–∫—É –∑–Ω–æ–≤—É
    }

    // –ü–ª–∞–≤–Ω–æ –ø—Ä–æ–∫—Ä—É—á—É—î–º–æ —Å—Ç–æ—Ä—ñ–Ω–∫—É –≤–Ω–∏–∑ –Ω–∞ –≤–∏—Å–æ—Ç—É –¥–≤–æ—Ö –∫–∞—Ä—Ç–æ–∫
    const { height: cardHeight } =
      gallery.firstElementChild.getBoundingClientRect();
    window.scrollBy({
      top: cardHeight * 2,
      behavior: 'smooth',
    });
  } catch (error) {
    // –ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –∑–∞–ø–∏—Ç—ñ ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    iziToast.error({
      title: 'Error',
      message: 'Something went wrong. Please try again later.',
      position: 'topRight',
    });
    console.error(error);
  } finally {
    // –•–æ–≤–∞—î–º–æ –ª–æ–∞–¥–µ—Ä –ø—ñ—Å–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
    hideLoader();
  }
}
