// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ –±—ñ–±–ª—ñ–æ—Ç–µ–∫—É axios –¥–ª—è HTTP-–∑–∞–ø–∏—Ç—ñ–≤
import axios from 'axios';

// –ö–ª—é—á API –¥–ª—è –¥–æ—Å—Ç—É–ø—É –¥–æ Pixabay
const API_KEY = '50309673-ea2b029f8f1cb6745c8643ce8';

// –ë–∞–∑–æ–≤–∞ URL-–∞–¥—Ä–µ—Å–∞ API Pixabay
const BASE_URL = 'https://pixabay.com/api/';

// –ö—ñ–ª—å–∫—ñ—Å—Ç—å –∑–æ–±—Ä–∞–∂–µ–Ω—å, —è–∫—ñ –±—É–¥—É—Ç—å –∑–∞–≤–∞–Ω—Ç–∞–∂—É–≤–∞—Ç–∏—Å—è –Ω–∞ –æ–¥–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É
const PER_PAGE = 15;

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å –∑–∞ –ø–æ—à—É–∫–æ–≤–∏–º –∑–∞–ø–∏—Ç–æ–º.
 * @param {string} query - –ü–æ—à—É–∫–æ–≤–∏–π –∑–∞–ø–∏—Ç, –≤–≤–µ–¥–µ–Ω–∏–π –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–µ–º
 * @param {number} page - –ù–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏ (–∑–∞ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—è–º 1)
 * @returns {Promise<Object>} - –ü–æ–≤–µ—Ä—Ç–∞—î –æ–±'—î–∫—Ç –∑ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º–∏ (totalHits, hits —Ç–æ—â–æ)
 */
export async function getImagesByQuery(query, page = 1) {
  // –ü–∞—Ä–∞–º–µ—Ç—Ä–∏ –∑–∞–ø–∏—Ç—É –¥–æ API
  const params = {
    key: API_KEY, // API –∫–ª—é—á
    q: query.trim(), // –ü–æ—à—É–∫–æ–≤–∏–π –∑–∞–ø–∏—Ç –±–µ–∑ –∑–∞–π–≤–∏—Ö –ø—Ä–æ–±—ñ–ª—ñ–≤
    image_type: 'photo', // –¢–∏–ø –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è ‚Äî —Ç—ñ–ª—å–∫–∏ —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ—ñ—ó
    orientation: 'horizontal', // –û—Ä—ñ—î–Ω—Ç–∞—Ü—ñ—è ‚Äî –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞
    safesearch: true, // –§—ñ–ª—å—Ç—Ä –±–µ–∑–ø–µ—á–Ω–æ–≥–æ –ø–æ—à—É–∫—É (–≤–∏–∫–ª—é—á–∞—î 18+ –∫–æ–Ω—Ç–µ–Ω—Ç)
    page, // –ù–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏
    per_page: PER_PAGE, // –ö—ñ–ª—å–∫—ñ—Å—Ç—å —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω–∫—É
  };

  // –ù–∞–¥—Å–∏–ª–∞—î–º–æ GET-–∑–∞–ø–∏—Ç –¥–æ API –∑ –ø–µ—Ä–µ–¥–∞–Ω–∏–º–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏
  const response = await axios.get(BASE_URL, { params });

  // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ –ª–∏—à–µ –Ω–µ–æ–±—Ö—ñ–¥–Ω—ñ –¥–∞–Ω—ñ –∑ –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ (–±–µ–∑ –∑–∞–π–≤–æ—ó –æ–±–≥–æ—Ä—Ç–∫–∏)
  return response.data;
}
